import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { map, catchError, retry } from 'rxjs/operators';
import { Storage } from '@ionic/storage';
import { ApiService } from './api.service';
import { TranslateService } from '@ngx-translate/core';
import { ToastService } from './toast.service';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { AuthService } from './auth.service';
import { EventProviderService } from './event-provider.service';

@Injectable({
  providedIn: 'root'
})
export class InterceptorService implements HttpInterceptor {
  userData: any;
  alertMessage: any;
  lang: any;
  key: string = 'n0TKP4K0#1^Vpm@%wwvY';
  cmp_api_key = environment.cmp_api_key;
  constructor(private storage: Storage, private apiService: ApiService, private translateService: TranslateService, private toast: ToastService, private router: Router,
   private eventService:EventProviderService) {
    // get translated alert message
    let interval = setInterval(() => {
      this.translateService.get('ALERTS').subscribe((res: any) => {
        this.alertMessage = res;
        clearInterval(interval);
      });
    }, 2000)
  }

  isJson(str) {
    try {
         JSON.parse(str);
         return true;
    } catch (e) {
        return false;
    }
  }
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const token = localStorage.getItem('auth');
    const refresh_token = localStorage.getItem('refresh_token');
    const authToken = localStorage.getItem('apiAuthToken');
    const public_token = localStorage.getItem('public_token');
    this.lang = localStorage.getItem('app_language');
    if(authToken && request.url.includes('sendOTP')){
      request = request.clone({
        setHeaders: {
          'Accept': 'application/json',
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
          // "Authorization": "Bearer "+authToken
        },
        body: (request.url.includes('sendOTP')) ? (<any>window).CryptoJSAesJson.encrypt((JSON.stringify({ ...request.body, 'auth': 'Bearer ' + authToken, 'authorization': 'Bearer ' + authToken })), this.key) :  ''
      });
    }
    if (token && public_token) {
      request = request.clone({
        setHeaders: {
          'Accept': 'application/json',
          'Authorization': 'Bearer ' + public_token
        },
        body: { ...request.body, 'auth': 'Bearer ' + token, 'authorization': 'Bearer ' + public_token }
      });
    }else if(public_token){
      request = request.clone({
        setHeaders: {
          'Accept': 'application/json',
          // 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
          'Authorization': 'Bearer ' + public_token
        },
        body: { ...request.body, 'auth': 'Bearer ' + public_token, 'authorization': 'Bearer ' + public_token}
      });
    }

   
    if(request.url.match('churchbase.site/category') || request.url.match('churchbase.io/category')){
      request = request.clone({
        setHeaders: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        url: (request.url),
        body: JSON.stringify({ ...request.body})
      });
    } else if(((request.url.match('manage.radiobase.org')) || (request.url.match('devmanage.radiobase.org'))) && !(request.url.match('visitsAnalytics'))){
        
        if(request.url.includes('getGuestToken')){
          request = request.clone({
            setHeaders: {
              'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
            },
            // this.isJson(request.body) ? JSON.stringify({ ...JSON.parse(request.body),  'app_lang': this.lang ? this.lang : 'en' }) :
          body: (request.url.includes('sendOTP') || request.url.includes('getGuestToken') || request.url.includes('guestcreatepledge')) ? (<any>window).CryptoJSAesJson.encrypt((JSON.stringify({ ...request.body })), this.key) :  ''
          })
        }
        if(request.url.includes("External/getPressBibleLugandaBooks")){
          request = request.clone({
          setHeaders: {
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },
          url: (request.url + !(request.url.match('app.churchprayerwall.com')) ? '/en' : ''),
          body: JSON.stringify({ ...request.body, 'app_lang': this.lang ? this.lang : 'en'})
        });
        }else if(!request.url.includes('getGuestToken') && !request.url.includes('sendOTP')){
          request = request.clone({
            setHeaders: {
              'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
            },
            // url: this.lang && !(request.url.match('app.churchprayerwall.com')) ? (request.url + '/' + this.lang) : request.url,
            body: JSON.stringify({ ...request.body, 'app_lang': this.lang ? this.lang : 'en'})
          });
        }

      }
      else if(request.url.includes("External/getPressBibleLugandaBooks")){
        request = request.clone({
          setHeaders: {
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },
          // url: (request.url + !(request.url.match('app.churchprayerwall.com')) ? '/en' : ''),
          body: JSON.stringify({ ...request.body, 'app_lang': this.lang ? this.lang : 'en'})
        });
      }
        else if(!(request.url.match('app.churchprayerwall.com')) && !(request.url.match('cmp.thruthebible.io')))  {
      request = request.clone({
        setHeaders: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        url: (request.url),
        body: JSON.stringify({ ...request.body, 'app_lang': this.lang ? this.lang : 'en'})
      });
    } else if(request.url.includes('cmp.thruthebible.io')) {
      request = request.clone({
        setHeaders: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        url: (request.url),
        body: JSON.stringify({ ...request.body, 'app_lang': this.lang ? this.lang : 'en'})
      });
    
   
} else if(!request.url.includes('getGuestToken') && !request.url.includes('sendOTP') && !(request.url.match('app.churchprayerwall.com'))) {
      request = request.clone({
        setHeaders: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        url:  this.lang && !(request.url.match('app.churchprayerwall.com')) ? (request.url + '/' + this.lang) : request.url,
        body: !(request.url.match('app.churchprayerwall.com')) ? JSON.stringify({ ...request.body}) : this.isJson(request.body) ? JSON.parse(request.body) : {...request.body, 'test': 'test'}
      });
    
   
}

    if (!request.headers.has('Content-Type') && !(request.url.match('app.churchprayerwall.com'))) {
      request = request.clone({

        setHeaders: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        }
      });
    }

    if((request.url.includes('cmp.thruthebible.io'))) {
      request = request.clone({
        setHeaders: {
          'Authorization': this.cmp_api_key,
          'content-type': 'application/json'
        },
      });
    }
    
    if((request.url.includes('cmp.thruthebible.io/teachings/search'))) {
      request = request.clone({
        setHeaders: {
          'content-type': 'application/json'
        },
      });
    }

    request = request.clone({
      headers: request.headers.set('Accept', 'application/json')
    });
    if((request.url.match('https://streamdb3web.securenetsystems.net/player_status_update'))){
      request = request.clone({
        headers: request.headers.set('Accept', '*/*')
      });
    }

    return next.handle(request).pipe(
      map((event: HttpEvent<any>) => {
        if (event instanceof HttpResponse) {
        }
        return event;
      }),
      catchError((error: HttpErrorResponse) => {
       if (error.status === 401 && (error.error.code === 'access_token_expired' || error.error.code === "access_token_not_found")) {
          this.storage.get('user').then(res => {
            if (res != null && res != 'guest') {
              this.userData = res;
              const data = {
                auth: 'Bearer ' + this.userData.auth.access_token,
                authorization: 'Bearer ' + this.userData.auth.refresh_token
              };
              this.apiService.updateToken(JSON.stringify(data)).subscribe(res => {
                this.userData.auth.access_token = res['access_token'];
                this.userData.auth.refresh_token = res['refresh_token'];
                this.storage.set('user', this.userData);
                localStorage.setItem('auth', this.userData.auth.access_token);
                localStorage.setItem('refresh_token', this.userData.auth.refresh_token);
              }, (err: any) => {
                this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.error_occured_try_again : 'Error Occured, Try Again'), (this.alertMessage ? this.alertMessage.error : 'ERROR'), "warning");
              });
            }
          });

        } else if((error.error.code === 'access_token_expired' || error.error.code === "access_token_not_found") && !error.url.includes('get_donorsources')){
          this.storage.get('user').then(res => {
            if (res != null && res != 'guest') {
              this.userData = res;
              const data = {
                auth: 'Bearer ' + this.userData.auth.access_token,
                authorization: 'Bearer ' + this.userData.auth.refresh_token
              };
              this.apiService.updateToken(JSON.stringify(data)).subscribe(res => {
                this.userData.auth.access_token = res['access_token'];
                this.userData.auth.refresh_token = res['refresh_token'];
                this.storage.set('user', this.userData);
                localStorage.setItem('auth', this.userData.auth.access_token);
                localStorage.setItem('refresh_token', this.userData.auth.refresh_token);
              }, (err: any) => {
                this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.error_occured_try_again : 'Error Occured, Try Again'), (this.alertMessage ? this.alertMessage.error : 'ERROR'), "warning");
              });
            }
          });
        } else if(!error.url.includes('getGuestToken') &&  error.status === 405){
          localStorage.setItem("errorURL", error.url);
        }else if (error.status != 500 && error.status != 403 && !(error.error.code === 'access_token_expired' || error.error.code === "access_token_not_found")) {
          if(!error.url.includes('analytics') && !error.url.includes('getGuestToken') && !error.url.includes('getChurchLang') && !error.url.includes('Content/content') && !error.url.includes('visitsAnalytics')  && !error.url.includes('googleseofields') && !error.url.includes('wasabisys.com') && !error.url.includes('api.ipify.org')  && !error.url.includes('ip-api.com')  && !error.url.includes('sendOTP') && !error.url.includes('dbt.io')  && !error.url.includes('public.radio.co')){
            // this.router.navigate(['/login']);
          }
        }
        return throwError(error);
      }));
  }

}