    getLatestSermon(){
        let youtube = 'youtube';
        let youtube2 = 'youtu.be';
        let vimeo = 'vimeo';
        let vimeo2 = 'www.vimeo';
        this.sermonService.getLatestSermon(this.ch_id).subscribe((res:any)=>{
            if(res.sermons){ 
               
                this.sermonData = res.sermons;
                if(res.sermons && res.sermons.sermon_video_url != null){
                    this.latest_sermon_video = true;
                    this.sermon_video_url = res.sermons.sermon_video_url;

                    if (this.sermon_video_url != '' && this.sermon_video_url != null) {
                        //this.sermon_video_url = '';
                       
                        if (this.sermon_video_url.includes(youtube)) {
                            this.sermon_video_url = this.sermon_video_url.replace('watch?v=', 'embed/');
                        } else if (this.sermon_video_url.includes(youtube2)) {
                            this.sermon_video_url = this.sermon_video_url.replace('youtu.be/', 'www.youtube.com/embed/');
        
                        } else if (this.sermon_video_url.includes(vimeo)) {
        
                            if (this.sermon_video_url.includes(vimeo2)) {
                                this.sermon_video_url = this.sermon_video_url.replace('www.vimeo.com/', 'player.vimeo.com/video/');
                            } else {
                                this.sermon_video_url = this.sermon_video_url.replace('vimeo.com/', 'player.vimeo.com/video/');
                            }
                        } else if(this.sermon_video_url.includes('facebook.com')){
                            // let matches = self.selectedSermon.video_link[0].match(/(\d*)/g);
                            // let fb_video_id = '';
                            // matches.forEach(element => {
                            //     if(element != ''){
                            //         fb_video_id = element;
                            //     }
                            // });
                            this.sermon_video_url = "https://www.facebook.com/plugins/video.php?href=" + this.sermon_video_url +"&show_text=false&height=375&appId";
                        }
                         else {
                            this.wasabi = true;
                            this.sermon_video_url = this.sermon_video_url;
                        }
                        
                        // sanitizing video url for security purpose
                        this.sermon_video_url = this.dom.bypassSecurityTrustResourceUrl(this.sermon_video_url);
                    }



                }else{
                    this.latest_sermon_video = false;
                }

                if(res.sermons.sermon_audio != null){

                    this.latest_sermon_audio = true;
                    this.audioUrl = res.sermons.sermon_audio;
                    if(this.audioUrl.includes("soundcloud.com")){
                        this.soundcloud = true;
                        this.audioUrl = "https://w.soundcloud.com/player/?url=" + this.audioUrl;
                        this.audioUrl = this.dom.bypassSecurityTrustResourceUrl(this.audioUrl);
                    }
                }else{

                    this.latest_sermon_audio = false;
                }

                this.audioTitle = res.sermons.title;
                this.isSermon = true;


               // this.latest_sermon_audio = res.sermon_audio ? res.sermon_audio : false;
            }
        });
    }



