    play(element: any, event, index){
        if(event){
            event.stopPropagation();
        }
       // this.playing_media_index = '';
       this.playing_media_index = index;
        this.isVideoPlay = 'Play';
        element.play();
    }

    onEndVideo(){
        clearTimeout(this.clearInterval);
        this.isVideoPlay = 'Pause';
        this.firstPlay = 'true';
        this.playing_media_index = '';
        this.show = false;
    }

    onVideoClick(event, element, index){
        if(event){
            event.stopPropagation();
        }
       
        if(this.isVideoPlay === 'Pause'){
            element.play();
            this.updateProgress(element)
            this.isVideoPlay = 'Play';
            this.playing_media_index = index;

        }else if(this.isVideoPlay === 'Play'){

            element.pause();
            clearTimeout(this.clearInterval);
           // this.playing_media_index = '';
            this.isVideoPlay = 'Pause';
        }
       

    }
    getwasabistyle(index): any{
        let block = 'block';
        if(this.firstPlay === 'false' && this.playing_media_index_for_display === index){
            return block;
        }else{
            return 'none';
        }

    }

