    loadedmetadata(ev, element){
        this.duration = element.duration;
        this.total_duration = this.intoMinutes(this.duration);
      
    }
// https://vimeo.com/17985665    
// https://soundcloud.com/user-103777231/kahani-suno-2-0-song-kaifi
    updateProgress(element2) {

   
       
        if(this.player && this.latest_sermon_audio){
            let seek = Math.floor(this.player.seek());
            this.current_duration = this.intoMinutes(seek);
            this.total_duration = this.intoMinutes(this.player.duration()); 
            this.progress = (seek / this.player.duration()) * 100 || 0;
        }else if(this.latest_sermon_video ){
        this.current_duration = this.intoMinutes(element2.currentTime);
        this.progress = ((element2.currentTime)/(this.duration)) * 100 || 0;
     
        }
      
        this.clearInterval = setTimeout(() => {
          this.updateProgress(element2);
        }, 1000)
      }

      intoMinutes(second) {
        // returns rounded off minutes and seconds
        var minutes = Math.floor(second / 60) || 0;
        var seconds = Math.floor(second - minutes * 60) || 0;
        let min;
        let sec;
    
        if (minutes < 10){
          min = minutes;
        } else {
          min = minutes.toFixed(2);
        }
        if (seconds < 10){
          sec = '0' + seconds;
        } else {
          sec = seconds;
        }
        return min + ':' + sec;
      }
    

      seek(event, element){

        if(this.player && this.latest_sermon_audio){
            let newValue = +this.range.value;
            let seek = Math.floor(newValue);
            seek = (newValue * this.player.duration()) / 100 ;
            this.player.seek(seek);
        }else if(this.latest_sermon_video){


            let newValue = event.target.value;
            let seek = Math.floor(newValue);
            seek = (newValue * this.duration)/100;
            element.currentTime = seek;
          this.progress = element.currentTime;

        }

        

      }

      onrangeClick(event){
        if(event){
            event.stopPropagation();
        }

      }



      getsoundcloudstyle(index):any{
        let block = 'block';
        
        if(this.playing_media_index_for_display === index){
            return block;
        }else{
            return 'none';
        }
      }

