import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ApiService } from 'src/services/api.service';
import { EventProviderService } from 'src/services/event-provider.service';
import { LoaderService } from 'src/services/loader.service';
import { ToastService } from 'src/services/toast.service';
import { Storage } from '@ionic/storage';
@Component({
  selector: 'app-subcribe-news',
  templateUrl: './subcribe-news.component.html',
  styleUrls: ['./subcribe-news.component.scss'],
})
export class SubcribeNewsComponent implements OnInit {
  ch_id: any;
  name: any = '';
  email: any = '';
  divClassForName: any;
  divClassForEmail: any;
  account_donor_id: any;
  

  constructor( private storage: Storage,public event_provider: EventProviderService, private api: ApiService,public modalController: ModalController,private loader: LoaderService,public toast: ToastService) { }


   // ZB changes start
  ngOnInit() {
    this.event_provider.getChurchData.subscribe(async (res: any) => {
      if ((Object.keys(res).length === 0)) {
        return;
      }

      this.ch_id = res.church.ch_id;
    });
    this.getUser();
  }
  getUser() {
    // Getting User from local storage
    this.storage.get('user').then(res => {
      if (res != null && res != 'guest') {
        this.account_donor_id = res.user.id;
      }

    });
  }

  subscribe(){
    

    if(this.name === ''){

      this.divClassForName = 'input-error';
    }else if(this.email === '' || !(this.email.match("[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{3,}"))){

      this.divClassForEmail = 'input-error';
    }else if(this.name !== '' && this.email !== ''){

      let body =JSON.stringify(
        {
          email: this.email,
          name: this.name,
          church_id: this.ch_id,
          account_donor_id: this.account_donor_id ? this.account_donor_id : ''
         }
      )

      this.loader.presentLoading().then(()=>{
        this.api.subscribeNewsLetter(body).subscribe((res:any)=>{
          if(res.status){
  
            this.modalController.dismiss();
            this.toast.presentToastWithOptions('Email subscribed','Thanks','success');
          //  console.log('status is: ', res);
          }else{
            this.modalController.dismiss();
            this.toast.presentToastWithOptions('Email has been already subscribed','Something Went Wrong','warning');
          }
        });
        this.loader.stopLoading();
      });
      
    }

  }

  dismiss(){
    this.modalController.dismiss({
      'dismissed': true
    });
  }
  // getStyle(){
  //   if(this.name == '' ){
  //      let color ='red' ;
  //     return color;

  //   }
  // }
  onKeyForName(event){
    this.divClassForName = '';
   // console.log('event value is: ', event);

  }


  onKeyForEmail(event){

    this.divClassForEmail = '';
  }


   // ZB changes end
}
