  seek() {
    let newValue = +this.range.value;
    let seek = Math.floor(newValue);
    seek = (newValue * this.player.duration()) / 100;
    this.player.seek(seek);
    if (newValue > this.progress) {
      this.eventProvider.seekAudio("seekForward");
    } else {
      this.eventProvider.seekAudio("seekBack");
    }
  }

  updateProgress() {
    let seek = Math.floor(this.player.seek());
    // this.eventProvider.seekAudio(seek);
    this.current_duration = this.intoMinutes(seek);
    this.progress = (seek / this.player.duration()) * 100 || 0;
    this.eventProvider.lastSeek(this.player.seek(), this.index, this.audioTitle, this.progress, this.current_duration, this.audioData.pubDate);
    setTimeout(() => {
      this.updateProgress();
    }, 1000);
  }

  forwardA() {
    if (this.platform.is('cordova')) {
      this.platform.ready().then(() => {
        // get current playback position
        this.audio_media.getCurrentPosition().then((position) => {
          let seek = Math.floor(position);
          seek = seek * 1000;
          this.audio_media.seekTo(seek + 10000);
        });
      })
    } else {
      let seek = Math.floor(this.player.seek());
      this.player.seek(seek + 10);
      this.eventProvider.seekAudio("seekForward");
    }
  }


