  backwardA() {
    if (this.platform.is('cordova')) {
      this.platform.ready().then(() => {
        // get current playback position
        this.audio_media.getCurrentPosition().then((position) => {
          let seek = Math.floor(position);
          seek = seek * 1000;
          this.audio_media.seekTo(seek - 10000);
          this.eventProvider.seekAudio("seekBack");
        });
      })
    }
    else {
      let seek = Math.floor(this.player.seek());
      this.player.seek(seek - 10);
      this.eventProvider.seekAudio("seekBack");
    }
  }
