import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController, NavParams } from '@ionic/angular';
import { ApiService } from 'src/services/api.service';
import { EventProviderService } from 'src/services/event-provider.service';
import { IdService } from 'src/services/id.service';


@Component({
  selector: 'app-general-search',
  templateUrl: './general-search.component.html',
  styleUrls: ['./general-search.component.scss'],
})
export class GeneralSearchComponent implements OnInit {

  searchData: any = [];
  isSearch: boolean = false;
  is_wordpress_enable: boolean;
  appPages: any;
  pagination: any;
  searchingword: any;
  ch_id: any;
  page: any = 1;
  church_id: any;
  searchWord: any;
  page_id: any;

  constructor(public modalController: ModalController, private router: Router,
    public idservice: IdService, public apicallService: ApiService,
    public location: Location, public event_provider: EventProviderService, private navParams: NavParams) {}
  ngOnInit() {
    this.page_id = this.navParams.data.PageId
    this.event_provider.getChurchData.subscribe(async (res: any) => {
      if ((Object.keys(res).length === 0)) {
        return;
      }

      this.appPages = res.apppages;
      this.ch_id = res.church.ch_id;
      if (res.is_wordpress_enable === '1') {
        this.is_wordpress_enable = true;

      } else if (res.is_wordpress_enable === '2') {
        this.is_wordpress_enable = false;
      }
    })

  }

  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }

  searchContent(event) {
    if (event.detail.value.length > 1) {

      this.searchWord = event.detail.value
      this.church_id = this.ch_id

      this.apicallService.searchInShow(this.church_id, this.page, this.searchWord).subscribe((res: any) => {
        if (res.status == true) {
          this.searchData = res.sermons
        }
        this.isSearch = true;
      })
    } else {
      this.isSearch = false;
      this.searchData = []
    }
  }
  getsermonpwa(id_series) {

    // this.loader.presentLoading().then(() => {
    // navigating to detail page
    this.router.navigate(['/show-detail', { id: id_series, page_id: this.page_id }]);
    this.dismiss()
    // });
  }
  speachtext(){
    alert('ok')
  }

}
