    intoMinutes(second) {
        var minutes: any = Math.floor(second / 60);
        var seconds: any = Math.floor(second - minutes * 60);
        if (minutes < 10) {
            minutes = "0" + minutes;
        }
        if (seconds < 10) {
            seconds = "0" + seconds;
        }

        return minutes + ":" + seconds;
    }

