    intervalFun(url_id){
        this.intervalCalling =  setInterval( () => {
            // console.log("intervall  ");
           this.apiSer.radioCoApi(url_id).subscribe((res:any)=>{  
             if((res.current_track.title != this.title) && this.audioUrl.includes("radio.co")){
                 // this.artImg = res.current_track.artwork_url_large
                 // this.title = res.current_track.title;
                 if(this.isPlaying){
                     this.sendLiveStreamingAnalytics(this.isPlaying);
                     
                 } 
                 if(this.title){
                     setTimeout(() => {
                        // if(!this.fromPage || this.fromPage == ""){
                         this.artImg = res.current_track.artwork_url_large
                         this.image = res.current_track.artwork_url_large
                         this.common.image = res.current_track.artwork_url_large    
                         this.title = res.current_track.title;
                         let author2 = this.title.split('-');
                         this.author = author2.length?  author2[0] : "Unknown" ;
                        
                         if(this.isPlaying){
                             this.createLockScreenPlayer();
                         }
                        
                      
                         var _body = {
                             url: this.audioUrl,
                             church_id: this.idService.getId(),
                             method: "analytics",
                             user_id: "",
                             device_id: this.deviceId,
                             song_name : author2.length?  author2[1] : "NA",
                             song_artist : this.author 
                         }
                         this.status = '';
                         this.apiSer.defalutUrlLike(_body).subscribe((result: any) => {
                             if (result.status) {
                                 if (result.message == 'Like') {
                                     this.status = '1'
                                 } else if (result.message == 'Dislike') {
                                     this.status = "0"
                                 }
                             }
                           }, err => {
                             console.log({error:err})
                           })
                        // }
                      }, 1500); 
                 } 
             }
           },err=>{
             console.log({error:err});
           })
     
         }, 2500);
       }


    clear(){
        clearInterval(this.intervalCalling)
    }

