        this.playing_media_index = ""
        this.current_total_duration = "";
        this.common.display_media_player_loader = true;
        this.study_image = false;
        this.is_today_study = false;
        this.timeInSecond = 0;
        this.audioIndex= -1;
        this.musicControls.destroy();
       
        this.fromPage ="";
        this.index = ""
