       intoMinutesTotalDuration(second) {
        var minutes = Math.floor(second / 60);
        var seconds = Math.floor(second - minutes * 60);
    
        let sec;
        sec = seconds < 10 ? '0' + seconds : seconds;
    
        return minutes + ':' + sec;
      }

    createAnalytics(metricType?, trackUrl?) {
    const object = {
      title: this.title,
      teaching_Type: 'Daily',
      app_section: this.is_today_study ? "Today's Study" : 'Study Section',
      media_format: 'Audio',
      track_url: trackUrl,
      duration : this.timeInSecond
    }
    // console.log(object)
    if (metricType == 'onFirstPlay') {
        delete object.duration;
        console.log("trigger", "study_program_start",JSON.stringify(object))
        this.timeInSecond =  0;
        clearTimeout(this.counterId);
        localStorage.setItem("firebaseDuration" , JSON.stringify(object))
        this.countStart();
    this.analyticsFirebase.logEvent('study_program_start', object)
    } else if (metricType == 'durationOfPlay') {
        var newobj = JSON.parse(localStorage.getItem("firebaseDuration"));
        newobj.duration = this.timeInSecond;
        console.log("trigger", "study_program_duration",JSON.stringify(newobj))
      this.analyticsFirebase.logEvent('study_program_duration', newobj)
    }
  }

  countStart() {
    // console.log(this. isPlaying , this.timeInSecond)
    if(this.isPlaying ) {
        this.timeInSecond = this.timeInSecond + 1;
        if(this.timeInSecond == 60){   
            this.createAnalytics("durationOfPlay");
            this.timeInSecond = 0;
        }
    }

    this.counterId = setTimeout(() => {
        this.countStart();
    }, 1000);
   
}


heardStudy( duration, audioData, method, complete?, audiosLength?){
    // audioData['book_id'] = audioData.book_id? audioData.book_id:'0';
    console.log("comments", duration, audioData, audioData.book_id, method, complete)
    var listenData=[]; 
    var data={
        id: method == 'ism-study' || method == 'study'  ? audioData.uuid : audioData.id,
        duration : duration && duration != 0.0 ? duration : this.current_duration,
        bookname: method == 'ism-study'? audioData.bible_book : "study",
        completeListner: complete ? complete : false,
        date: Date.now(),
        studyType: method,
        art_url: audioData.art_url,
        name:audioData.name,
        book_id: method == 'ism-study'? audioData.book_id : "study",
        complete_bookname: this.bibleBook_name  
    }

    if(audioData.audio_format ==  'Daily'){
        audioData['complete_bookname'] = this.bibleBook_name;
        localStorage.setItem("recentAudio" , JSON.stringify(audioData));
    }
    if(localStorage.getItem("listenStudy")){
        listenData = JSON.parse(localStorage.getItem("listenStudy"));
        var idx = listenData.findIndex(x=> x.id == (method == 'ism-study'|| method == 'study'  ? audioData.uuid : audioData.id))
        if(idx > -1){
            listenData[idx].duration = this.current_duration;
            listenData[idx].completeListner = complete ? complete : listenData[idx].completeListner;
            listenData[idx].date = complete ? Date.now() : listenData[idx].date;
            localStorage.setItem("listenStudy", JSON.stringify(listenData))
        }else{
            listenData.push(data);
            localStorage.setItem("listenStudy", JSON.stringify(listenData))
        }
    }else{
        listenData.push(data);
        localStorage.setItem("listenStudy", JSON.stringify(listenData))
    }

    setTimeout(() => {
    if(method == "ism-study"){
    var read_audios = JSON.parse(localStorage.getItem("listenStudy"));
    console.log(audioData.bible_book?audioData.bible_book : (this.common.audioData? this.common.audioData.bible_book: audioData.bible_book))
    read_audios = read_audios ? read_audios.filter(x=>x.bookname == (audioData.bible_book?audioData.bible_book : (this.common.audioData? this.common.audioData.bible_book: audioData.bible_book)) && x.completeListner == true):[];
    if(read_audios && read_audios.length > 0 ){
      this.readAudio = read_audios.length;
    }else{
      this.readAudio = 0;
    }

    console.log(this.tt_all_audios, audiosLength,"audiosssssss")
    if(audiosLength && audiosLength > 0){
        if(audiosLength && audiosLength > 0){
            this.progressForChapter = (this.readAudio / audiosLength) * 100 || 0;
            if(this.audioIndex ==  this.study_ttbAudios.length - 1){
                this.openmodalFinish(audioData, data);
            }
            audiosLength = 0;
        }
    }else{
        if(this.tt_all_audios && this.tt_all_audios.length > 0){
            this.progressForChapter = (this.readAudio / this.tt_all_audios.length) * 100 || 0;
            if(this.audioIndex ==  this.study_ttbAudios.length - 1){
                this.openmodalFinish(audioData, data);
                // this.router.navigate(["/study-completed", {
                //     audioData: JSON.stringify(audioData),
                //     bookData: JSON.stringify(data)
                // }])
            }
        }
    }
    }
    }, 1000);
}

    async openmodalFinish(audioData, data){
        var listenData = localStorage.getItem("listenStudy")? JSON.parse(localStorage.getItem("listenStudy")): [];
        var filterData = listenData.filter(x=>x.book_id == data.book_id);
        if(filterData && filterData.length > 0 && data.completeListner == true && filterData.length ==  this.study_ttbAudios.length){
            var idx = filterData.findIndex(x=>x.completeListner == false);
            if(idx == -1 ){
                const modal = await this.modalController.create({
                    component: FinishComponent,
                    backdropDismiss: true,
                    // cssClass: 'BibleModal',
                    componentProps: {
                        audioData: JSON.stringify(audioData),
                        bookData: JSON.stringify(data)
                     
                    }
                });
                return await modal.present();
            }
        }
        // this.bookData = JSON.parse(this.navParams.get('dataBook'));
        // this.audioData =  JSON.parse(this.navParams.get('audioData'));

}
