  getTouches(evt) {
    return evt.touches ||             // browser API
        evt.originalEvent.touches; // jQuery
  }
  handleTouchStart(evt) {
    const firstTouch = this.getTouches(evt)[0];
    this.xDown = firstTouch.clientX;
    this.yDown = firstTouch.clientY;
  };

  handleTouchMove(evt) {
    if ( ! this.xDown || ! this.yDown ) {
      return;
    }

    var xUp = evt.touches[0].clientX;
    var yUp = evt.touches[0].clientY;

    var xDiff = this.xDown - xUp;
    var yDiff = this.yDown - yUp;

    if ( Math.abs( xDiff ) > Math.abs( yDiff ) ) {/*most significant*/
      if ( xDiff > 0 ) {
        /* left swipe */
      } else {
        /* right swipe */
      }
    } else {
      if ( yDiff > 0 ) {
        /* up swipe */
        console.log("console to swip up ");
      } else {
        /* down swipe */
        console.log("console to swip down ");
        // this.service.defaultValue = "revertChange"
        this.dismiss();
        this.firstPlay = false;
        this.myAudioPlay = true;
        this.service.show = false
        if(this.audioService.isAudioPlay == 'Pause' || this.audioService.isAudioPlay == ''){
          this.wavesCondition = "stop";
          this.newWavesCondition = "stop";  
        }else if(this.audioService.isAudioPlay == 'Play'){
          this.wavesCondition = "play";
          this.newWavesCondition = "play";          
        }

      }
    }
    /* reset values */
    this.xDown = null;
    this.yDown = null;
  };


  seek() {
        
    if (this.platform.is('cordova') && !this.platform.is("android")) {
      this.platform.ready().then(() => {
        this.audioService.audiomedia.getCurrentPosition().then((position) => {
          if(position && position >= 0) {
            // get current playback position
            let newValue = +this.range.value;
            let val = this.audioService.audiomedia.getDuration() * (newValue / 100) * 1000;
            if (val) {
              this.audioService.audiomedia.seekTo(val);
            }
          }
        })
      })
    } 
    else if(this.platform.is("cordova") && this.platform.is("android")){
      let newValue = +this.range.value;
      (<any>window).mediaplayer.current(success => { 
        console.log(success); 
        let duration = (Math.floor(success.duration))/1000;
        let seekvalue:any = ((duration * (newValue / 100)));
        console.log("finnal console," , newValue  ,  ( parseInt(seekvalue) * 1000).toString() );
        (<any>window).mediaplayer.seek({ seekTo:  (parseInt(seekvalue) * 1000).toString() } ,success => { console.log(success); }, fail =>{ console.log(fail); });
      }, fail =>{ console.log(fail); })
    } 
    else {
      console.log(this.range.value , "valusssss")
      let newValue = +this.range.value;
      let duration = this.audioService.player.duration();
      this.audioService.player.seek(duration * (newValue / 100));
    }
  }



