  async rateSpeed(){
   
    const actionSheet = await this.actionSheetController.create({
      header: 'Auto Speed',
      buttons: [
        {
        text: '0.5x',
        role: 'destructive',
        icon: this.speedViewer == '0.5'? 'checkmark' : '',
        handler: () => {
          this.speedViewer = '0.5'
          if(this.platform.is("ios")){
            this.audioService.audiomedia.setRate(0.5)
          }else{
          this.audioService.player.rate(0.5)
          }
        }
      },
      {
        text: '1x',
        role: 'destructive',
        icon: this.speedViewer == '1'? 'checkmark' : '',
        handler: () => {
          this.speedViewer = '1'
          if(this.platform.is("ios")){
            this.audioService.audiomedia.setRate(1)
          }else{
          this.audioService.player.rate(1)
          }
        }
      },
      {
        text: '1.5x',
        role: 'destructive',
        icon: this.speedViewer == '1.5'? 'checkmark' : '',
        handler: () => {
          this.speedViewer = '1.5'
          if(this.platform.is("ios")){
            this.audioService.audiomedia.setRate(1.5)
          }else{
          this.audioService.player.rate(1.5)
          }
        }
      },
      {
        text: '2x',
        role: 'destructive',
        icon: this.speedViewer == '2'? 'checkmark' : '',
        handler: () => {
          this.speedViewer = '2'
          if(this.platform.is("ios")){
            this.audioService.audiomedia.setRate(2)
          }else{
            this.audioService.player.rate(2)
          }
         
        }
      },
       {
        text: 'Cancel',
        icon: 'close',
        role: 'cancel',
        handler: () => {
         
        }
      }]
    });
    await actionSheet.present();
    // this.audioService.player.rate(2)
  }

  changeAudio(flag){
 
    if(flag == 'next'){
      this.audioService.index = this.audioService.index + 1;
    }else{
      this.audioService.index = this.audioService.index -1
    }
    var audio : string =  environment.cmp_audioLink+this.audioService.tt_all_audios[this.audioService.index].uuid; 
    setTimeout(() => {
      this.audioService.study_ttbAudios = new Array(this.audioService.tt_all_audios.length).fill(false);
      this.audioService.audioIndex = this.audioService.index
      this.audioService.study_ttbAudios[this.audioService.index] = true;
    
      this.audioService.fromPage = 'ism-study';
      this.audioService.title = this.audioService.tt_all_audios[this.audioService.index].name  
      this.audioService.image = this.audioService.image;
      // this.audioService.isAudioPlayShows = !pause;
      this.audioService.liveStreaming = false;  
      this.audioService.tt_all_audios[this.audioService.index].title = this.audioService.tt_all_audios[this.audioService.index].name;
      this.audioService.tt_all_audios[this.audioService.index].image = this.audioService.image
      this.service.method =  'ism-study';
      this.audioService.audioUrl = audio;
      this.service.audioData = this.audioService.tt_all_audios[this.audioService.index];
      this.audioService.audioUrl = audio;
      this.audioService.fromPage = 'ism-study';
      this.audioService.startAudio(audio);
    }, 500);



  }





