  openPdf(newBypassLink){
    if (this.platform.is('cordova')) { 
      let path = this.file.dataDirectory;
      const transfer = this.ft.create();
      // console.log('trnasfer created: ', transfer);
      transfer.download(newBypassLink, `${path}myfile.pdf`).then((res: any)=>{
        //  console.log('response from download: ', res);
          let url2 = res.toURL();
          // console.log('to url is: ', url2);
          if(this.platform.is('ios' || 'iphone')){
              this.documentviewer.viewDocument(encodeURI(url2), 'application/pdf', {});
          }else if(this.platform.is('android')){
             // console.log('inside the esle: ');
              this.fileOpener.open(url2, 'application/pdf');
          }
      });
    }else{
     this.iab.create(newBypassLink, '_blank', this.options);
    }

    
  }

  async presentActionSheet(newBypassLink) {
    const actionSheet = await this.actionSheetCtrl.create({
      header: 'Actions',
      buttons: [
        {
          text: 'Open',
          role: 'selected',
          handler: () => {  
            this.openPdf(newBypassLink);
          
          }  
        },
        {
          text: 'Download',
          handler: () => { 
            this.downloadService.downloadNotes(newBypassLink); 
           // console.log('download clicked');  
          }  
        },
        {
          text: 'Cancel',
          role: 'destructive',
          handler: () => {  
            actionSheet.dismiss();
          //  console.log('cancel clicked');  
          }  
        },
      ],
    });

    await actionSheet.present();
  }

getUniqueIdOld(parts: number): string {
  const stringArr = [];
  for(let i = 0; i< parts; i++){
      // tslint:disable-next-line:no-bitwise
      let S4 = (((1 + Math.random()) * 0x1000000) | 0).toString(16).substring(1);
      // let random_index = Math.random() * parts;
      //  S4 = S4.slice(0, random_index) + ar[i] +  S4.slice(random_index, 5);
      stringArr.push(S4);
  }
  return stringArr.join('-');
}
  getUniqueId(parts: number): string {
    const stringArr = [];
    let ar= ['d','e','r','n','a','g'] 
    for(let i = 0; i< parts; i++){
        // tslint:disable-next-line:no-bitwise
        let S4 = (((1 + Math.random()) * 0x100000000) | 0).toString(36).substring(1);
        let random_index = Math.random() * parts;
         S4 = S4.slice(0, random_index) + ar[i] +  S4.slice(random_index, 5);
        stringArr.push(S4);
    }
    return stringArr.join('');
}

