  async getDesign() {
    // getting theme from local storage
    if (localStorage.getItem("church_data")) {
      this.theme = JSON.parse(localStorage.getItem("church_data")).church;
      console.log(JSON.parse(localStorage.getItem("church_data")),"deesign")
      
      
    
      
      if (this.theme.ch_id != "" && this.theme.ch_id != null) {
        // getting latest theme from service if church_id is there
        // this.getChurchDetails(this.theme.ch_id);
        this.getDEfaultLang(environment.church_id);
      } else {
        // getting latest theme from service if church_id is NOT there
        if (this.platform.is("cordova")) {
          // this.theme.ch_id = environment.church_id;
          // this.getChurchDetails(environment.church_id);
          this.getDEfaultLang(environment.church_id);
        } else {
          if (environment.church_id) {
            // this.getChurchDetails(this.church_id);
            this.getDEfaultLang(environment.church_id);
          }
        }
      }
      this.themeStored = true;
    } else {
      // getting latest theme from service if church_id is NOT there
      if (this.platform.is("cordova")) {
        // this.theme.ch_id = environment.church_id;
        // this.getChurchDetails(environment.church_id);
        this.getDEfaultLang(environment.church_id);
      } else {
        if (this.church_id) {
          // this.getChurchDetails(this.church_id);
          this.getDEfaultLang(environment.church_id);
        }
      }
    }
  }


  
checkLanguage(churchId) {
  this.apiService.getAllLanguages({ church_id: churchId }).subscribe((res: any) => {
      const lang: any = localStorage.getItem('app_language');
      if (res.status) {
          this.languages = res.languages;
          const result = this.languages.some(item =>
              item.lang_code === lang
          )
          this.languages.forEach((elem) => {
              this.languageCodes.push(elem.lang_code);
              if (result) {
                  if (lang == elem.lang_code) {
                      let app_language = {
                          code: elem.lang_code,
                          name: elem.lang,
                          bible_id: elem.bible_id,
                          languages: this.languages,
                          dam_id : elem.dam_id,
                          audio_id: elem.audio_id
                      };
                      this.storage.set('app_language', app_language);
                  }
              } else {
                  let app_language = {
                      code: 'en',
                      name: 'English',
                      bible_id: '78a9f6124f344018-01',
                      languages: this.languages,
                  };
                  this.storage.set('app_language', app_language);
                  localStorage.setItem('app_language', app_language.code);
                  this.translate.setDefaultLang(app_language.code);
              }
          })
      } else {
        this.languages = [{
          bible_id: '78a9f6124f344018-01',
          lang: 'English',
          lang_code: 'en',
          dam_id : 'ENGESV'
      }, {
          bible_id: '',
          lang: 'Spanish',
          lang_code: 'es',
          dam_id : 'SPNWTC'
      }, {
          bible_id: '',
          lang: 'Arabic',
          lang_code: 'ar',
          dam_id : 'ARBVDV'
      }, {
          bible_id: '',
          lang: 'French',
          lang_code: 'fr',
          dam_id : 'FRNDBY'
      }];
          const result = this.languages.some(item => item.lang_code === lang);
          this.languages.forEach((elem) => {
              this.languageCodes.push(elem.lang_code);
              if (result) {
                  if (lang == elem.lang_code) {
                      let app_language = {
                          code: elem.lang_code,
                          name: elem.lang,
                          bible_id: elem.bible_id,
                          languages: this.languages
                      };
                      this.storage.set('app_language', app_language);
                  }
              } else {
                  let app_language = {
                      code: 'en',
                      name: 'English',
                      bible_id: '78a9f6124f344018-01',
                      languages: this.languages
                  };
                  this.storage.set('app_language', app_language);
                  localStorage.setItem('app_language', app_language.code);
                  this.translate.setDefaultLang(app_language.code);
              }

          })
      }
      this.translate.addLangs(this.languageCodes);
  })
}

  async isUserLoggedIn() {
    var client_type = "";
    // this.loader.presentLoading().then(() => {
    // getting user from local storage
    this.storage.get("user").then(async (val: any) => {
      if (val !== "guest" && val !== null) {
        this.getDesign();
        this.router.navigate([this.router.url]);
        client_type = "user";
      } else {
        //removing user data from local storage
        await this.storage.remove("user").then((res) => {});
        client_type = "guest";
        this.apiService
          .analyticsPerVisit(
            environment.church_id,
            this.platform.platforms()[0],
            client_type
          )
          .subscribe(
            (res: any) => {},
            (err: any) => {}
          );

        this.platform.resume.subscribe(
          (res: any) => {
            console.log(res);
            this.apiService
              .analyticsPerVisit(
                environment.church_id,
                this.platform.platforms()[0],
                client_type
              )
              .subscribe(
                (res: any) => {},
                (err: any) => {}
              );
          },
          (err: any) => {}
        );
        // getting ID from ID SERVICE which is set by HOME page or
        // GIVING Pages (External pages which comes with church ID)
        let id = this.idService.getId();

        // getting theme from local storage
        await this.storage.get("theme").then((value) => {
          if (value && value.ch_id != id && id) {
            // THIS PART RUNS WHEN CHURCH ID FROM LOCAL
            // STORAGE DOES NOT MATCH WITH CHURCH ID FROM SERVICE(latest church id)

            // removing theme from local storage and then calling latest theme
            this.storage.remove("theme").then((val) => {
              // this.getChurchDetails(id);
              this.getDEfaultLang(id);
              //  this.getDesign();
              //  console.log('Removing THeme');
            });
          } else if (value) {
            // THIS PART RUNS WHEN CHURCH ID FROM LOCAL
            // STORAGE DOES MATCHES WITH CHURCH ID FROM SERVICE(latest church id)
            this.getDesign();
            // console.log('Theme Already Available');
          } else {
            // THIS PART RUNS WHEN there is no theme
            // in local storage (New Church)
            // this.getChurchDetails(id);
            this.getDEfaultLang(id);
            // this.getDesign();
            // console.log('New');
          }
        });
      }
    });
    // });
  }

