  async showBlockAppAlert(Message) {
    const alert = await this.alertController.create({
      cssClass: "app-block-alert",
      // header: `<img src="../assets/img/block.svg">`,
      message: `<img src="../assets/img/block.svg"> <p>${Message} </p>`,
      backdropDismiss: false,
    });

    await alert.present();
  }

  async selectLanguagePopup() {
    const alert = await this.alertController.create({
      cssClass: "my-custom-class",
      header: "Select Language",
      backdropDismiss: false,
      inputs: [
        {
          name: "English",
          type: "radio",
          label: "English",
          value: "en",
          checked: true,
        },
        {
          name: "Spanish",
          type: "radio",
          label: "Spanish",
          value: "es",
        },
      ],
      buttons: [
        {
          text: "Ok",
          handler: (value) => {
            console.log(value);
            localStorage.setItem("app_language", value);
            localStorage.setItem("is_lang_selected", "true");
            this.is_lang_selected = "true";
            this.translate.setDefaultLang(value);
            this.getChurchDetails(this.church_id);
          },
        },
      ],
    });

    await alert.present();
  }

 
