import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';

@Component({
  selector: 'app-languagepopup',
  templateUrl: './languagepopup.component.html',
  styleUrls: ['./languagepopup.component.scss'],
})
export class LanguagepopupComponent implements OnInit {
  languages:any = [];
  searchWord:any;
  isSearch:boolean = false;
  languagefind:any;
  constructor(public navParams:NavParams, public modalController:ModalController) { }

  ngOnInit() {
   this.languages = this.navParams.data.languages;
   console.log(this.languages);
    
  }
  

  searchContent(event){
    if (event.detail.value.length > 1) {
      console.log(event.detail.value);
      this.searchWord = event.detail.value
      this.languagefind = this.languages.filter(x => x.lang.toLowerCase().includes(this.searchWord.toLowerCase()));
      console.log(event.detail.value);
      console.log(this.languagefind);
      // this.languagefind =  self.allSermons.filter(x => x.id_series === this.sermonid);
      if(this.languagefind.length > 0){
        this.isSearch = true;
      }
    } else {
      this.isSearch = false;
      // this.searchData = []
    }
  }

  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }

  selectLang(lang){
    console.log(lang , "add lang")
    this.modalController.dismiss({
      'dismissed': true,
       selectLang: lang,
    });
  }

  

}
