import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalController, NavParams } from '@ionic/angular';
import { Storage } from '@ionic/storage';
import { environment } from 'src/environments/environment';
import { ApiService } from 'src/services/api.service';
import { ToastService } from 'src/services/toast.service';

@Component({
  selector: 'app-finish',
  templateUrl: './finish.component.html',
  styleUrls: ['./finish.component.scss'],
})
export class FinishComponent implements OnInit {

  audioData:any;
  bookData:any;
  nextbookData:any;
  loader: boolean = true;
  constructor(public navParams:NavParams, public modalController:ModalController, public storage:Storage, public apiService :ApiService,
    public toast:ToastService, public router:Router) {
    this.storage.get('app_language').then((res) => {
      if(res.bible_id) {
        this.getBibleBooks(res.bible_id)
      } else {
        this.getBibleBooks('78a9f6124f344018-01')
      }
    
    });
   }

  ngOnInit() {
    
    // this.todayStudy = (this.navParams.get('todayStudy')) ? JSON.parse(this.navParams.get('todayStudy')) : "";
    this.bookData = JSON.parse(this.navParams.get('bookData'));
    this.audioData =  JSON.parse(this.navParams.get('audioData'));
    console.log(this.audioData,  this.bookData);
  }

  dismiss() {
    this.modalController.dismiss({
        'dismissed': true
    });
  };

  getBibleBooks(bible_id){
    this.apiService.getLanguageBooksListTeachings(bible_id).subscribe( (res: any) => {
      this.loader = false;
      if (res){
        console.log(res)
        var idx  = res.findIndex(x=>x.id == this.bookData.book_id);
        if(idx > -1){
          this.nextbookData = res[idx+1];
         console.log(this.nextbookData , "ok find book");
        }
      }
    });
  }

  openNextBook(){
    if(this.nextbookData){
      this.router.navigate(['ism-teachings-videos', {
        book_id: this.nextbookData.id,
        book_name: this.nextbookData.name,
        church_id : environment.church_id,
        book_name_eng : this.nextbookData.book_name_eng
      }]);
      this.dismiss();
    }else{
      this.dismiss();
      this.toast.presentToastWithOptions("Book Not Found", "", "success")
    }
   
  }
}
