import { Component, OnInit } from '@angular/core';
import {ModalController} from "@ionic/angular";
import {Router} from "@angular/router";
import {ApiService} from "../../../services/api.service";
import {LoaderService} from "../../../services/loader.service";
import {ToastService} from "../../../services/toast.service";
import {TranslateService} from "@ngx-translate/core";
import {Location} from "@angular/common";


@Component({
  selector: 'app-page-password-modal',
  templateUrl: './page-password-modal.component.html',
  styleUrls: ['./page-password-modal.component.scss'],
})
export class PagePasswordModalComponent implements OnInit {

  entered_password: any = '';
  page_password: any;
  alertMessage: any;
  success:any;
  error:any;
  errorText:any;
  constructor(
      public modalController: ModalController,
      private apiService: ApiService,
      private loader: LoaderService,
      private toast: ToastService,
      private translate: TranslateService,
      private location: Location,
    
  ) {
    let interval = setInterval(() => {
      this.translate.get('ALERTS').subscribe( (res: any) => {
          this.alertMessage = res;
          // this.getDesign();
          clearInterval(interval);
      });
  }, 3000);
  }

  ngOnInit() {
    this.translate.get("success_toast_password").subscribe( (res: any) => {
      this.success = res;
  });
  this.translate.get("error_toast_password").subscribe( (res: any) => {
    this.error = res;
});

this.translate.get("error_text_toast_password").subscribe( (res: any) => {
  this.errorText = res;
});
  }


  dismiss(status) {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    
    this.location.back();
    this.modalController.dismiss({
      status: status
    });
  }

  enterPage(){
    if (this.entered_password === this.page_password){
      this.toast.presentToastWithOptions(this.success, "", "success");
      this.modalController.dismiss({
        status: true
      });
    } else {
      this.toast.presentToastWithOptions(this.error, this.errorText, "warning");
    }
  }

}

