import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from "@ionic/angular";
import { LoaderService } from 'src/services/loader.service';


@Component({
  selector: 'app-listening-modal',
  templateUrl: './listening-modal.component.html',
  styleUrls: ['./listening-modal.component.scss'],
})
export class ListeningModalComponent implements OnInit {
  constructor(
    public modalController: ModalController,
    public loader: LoaderService
  ) { }

  @Input() audioList: string;
  @Input() index: number;
  @Input() session: number;

  volume: any = 25;
  volumeRequest: any = {
    volume: {
      level: 10,
      muted: false,
    }
  };



  ngOnInit() {
    console.log(this.index, this.audioList, this.session)
    this.loader.stopLoading();
  }

  dismiss() {

    // function stopSession () {
    // Also stop the session (if )
    var _session: any = this.session;
    _session.stop(function () {

    })
    this.modalController.dismiss({
      'dismissed': true
    });
  }
  




  volumeControl(event) {
    var self = this;

    var mute: boolean = false;
    if (event.target.value == 0) {
      mute = true
    }

    self.volumeRequest.volume.level = event.target.value;
    self.volume = event.target.value;

    // var session:any= self.session
    // session.setReceiverVolumeLevel(event.target.value,  function (success) {
    // console.log("volume controll")
    // })
  }

  muted: boolean = false;
  mutedCall() {
    var self = this;
    console.log("loggggggg");
    if (self.muted == true) {
      self.muted = false
      console.log(self.muted)
      var session: any = self.session
      // session.setReceiverVolumeLevel(self.volume,  function (success) {
      //   console.log("volume controll")
      //   })

    } else {
      self.muted = true
      console.log(self.muted)
      var session: any = self.session
      session.setReceiverMuted(true, function (success) {
        console.log("volume controll")
      })
    }
  }
}
