import { Component, OnInit } from '@angular/core';
import {ModalController} from "@ionic/angular";
import {ApiService} from "../../../services/api.service";

@Component({
  selector: 'app-images-library',
  templateUrl: './images-library.component.html',
  styleUrls: ['./images-library.component.scss'],
})
export class ImagesLibraryComponent implements OnInit {

  check_images: any = ['../../assets/img/live_stream_bg_2.jpg', '../../assets/img/user_icon.png', '../../assets/img/live_stream_bg_2.jpg', '../../assets/img/live_stream_bg_2.jpg'
    , '../../assets/img/live_stream_bg_2.jpg', '../../assets/img/live_stream_bg_2.jpg', '../../assets/img/live_stream_bg_2.jpg'
    , '../../assets/img/live_stream_bg_2.jpg', '../../assets/img/live_stream_bg_2.jpg', '../../assets/img/live_stream_bg_2.jpg'
    , '../../assets/img/live_stream_bg_2.jpg', '../../assets/img/live_stream_bg_2.jpg', '../../assets/img/live_stream_bg_2.jpg'];
  images: any = [];
  display_loader: boolean = false;

  constructor(
      public modalController: ModalController,
      private api: ApiService
  ) { }

  ngOnInit() {}

  dismissModal(){
    this.modalController.dismiss({
      selected_image: ''
    });
  }

  ionViewWillEnter(){
   this.getImages();
  }

  getImages(){
    this.display_loader = true;
    this.api.getLibraryPhotos().subscribe( (res: any) => {
      if (res && res.status){
        this.images = res.result;
        this.display_loader = false;
      }
    });
  }

  selectImage(image){
    console.log(image , "tpeee lib, rib,,")
    this.modalController.dismiss({
      selected_image: image
    });
  }
}
