import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DarkModeService {

  dark_mode: boolean = false;

  constructor() { }

  getDark_mode(){
    var mode = localStorage.getItem("app--mod");
    if(mode == "false"){
      return  false;
    }else{
      return true
    }
    // console.log(mode);
    // return this.dark_mode;
  }

  setDark_mode( val ){
    localStorage.setItem("app--mod", val);
    this.dark_mode = val;
  }

}
