import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs'; 
import  {io} from 'socket.io-client';
import { Socket } from 'ngx-socket-io';  


@Injectable({
  providedIn: 'root'
})
export class SocketService {

  observer = new BehaviorSubject<any>('');
  constructor(private socket: Socket) { }

  
  //  private socket = io('localhost:3001');


  public sendAnalytics(data : any){
   
    this.socket.emit('on-received-analytics', data);
  }


 public receivedAnalytics(){
    this.socket.connect();
   
    this.socket.fromEvent('streaming-analytics-data').subscribe((data: any) => {
         
      this.observer.next(data);
     
  
    })
  }


}


