@Injectable({
  providedIn: 'root'
})
export class CastingService {
  session: any = '';
  castingActive:boolean=false;
  playURl:any;
  receiver:any;
  isCastingActive : any = [];
  castingPage:any;
  _media:any;
  pageName:any;
  index:any;
  constructor(public event_provider: EventProviderService, public common: CommonService,
    public toast:ToastService) {
    // cordova plugin add https://github.com/jellyfin/cordova-plugin-chromecast.git

  }

  


  async createChromeCasting(audio){
  
    var obj = this; 
      var appId = (<any>window).chrome.cast.media.DEFAULT_MEDIA_RECEIVER_APP_ID;
      console.log(appId , "appID");
      let apiConfig = await new (<any>window).chrome.cast.ApiConfig(new (<any>window).chrome.cast.SessionRequest(appId), function sessionListener(session){
        console.log(session,"from session");
        if(session){
          console.log("from session is 1c");
          obj.loadMedia(session, audio, obj.pageName, obj.index);
        }else{
          console.log("from session is else");
        }
      
      },function receiverListener(receiverAvailable){
        
          setTimeout(() => {
            console.log(receiverAvailable , "reciver available");
          obj.receiver = receiverAvailable;
            if(obj.receiver == "available"){
              console.log("receiver available");
              // obj.requestSession(audio , flag);
            }
            else{
              // this.toast.presentToastWithOptions("Device unavailable", "Chrome Cast", "warning");
                // obj.castingActive = false;
            }
        }, 1000);
         
        });
      
        setTimeout(() => {
          console.log(apiConfig, obj.receiver , "config successings")
              console.log(apiConfig ,"connfig if receiver availabble", obj.receiver);
              (<any>window).chrome.cast.initialize(apiConfig, function (success) {
                console.log("success" , success);
                if(success == "success"){
                  console.log("success is going to checking devie");
                
                }
              }, function(error){console.log("app initialize error", error)});
            }, 500);
    
    
  }
  
  requestSessionssssssss(audio, fromPage,index){
    if(fromPage === 'radio-player'){
      this.castingActive = true;
      this.castingPage = fromPage;
    }else if(fromPage ==='sermon-detail'){
      this.castingActive = true;
      this.castingPage = fromPage;
    }
    else{
      // this.isCastingActive = new Array<any>(this.isCastingActive).fill(false);
      this.isCastingActive[index] = true;
      this.castingPage = fromPage;
      this.castingActive = false;
    }
  }

  
  requestSession(audio, fromPage,index){
    var obj = this;
    if(obj.receiver == 'available'){
      console.log("ennterrr in reqse");
      obj.pageName = fromPage;
      obj.index = index;
      (<any>window).chrome.cast.requestSession( (session) => {
        console.log("session acp",session);
       
          // obj.loadMedia(session, audio , flag);
          setTimeout(() => {
            console.log("session acp-----",session);
            obj.createChromeCasting(audio);
          }, 1500);
       
      },
         (error:any) => {
          console.log(error, "request session error");
          this.toast.presentToastWithOptions(error.description,'Chrome Cast','warning')
          obj.castingActive = false;
          var numeroPerguntas = 10; 
          obj.isCastingActive = new Array<any>(numeroPerguntas).push(false);
          obj._media = '';
          obj.session= '';
        // obj.scanroutes();
      }) 
      // setTimeout(() => {
      //    console.log("sessionListnerrrrr");
      //    obj.gettingSessinn(audio , flag);
      // }, 2000);
    }else{
      this.toast.presentToastWithOptions('Device unavailable','Chrome Cast','warning');
      this.createChromeCasting(audio);
    }
  }

 

  changeAudio(url, fromPage, index){
    var obj= this;
    obj.pageName = fromPage;
    obj.index = index;
    obj.loadMedia(obj.session,url, fromPage , index)
  }


  loadMedia(_session, audio,fromPage, index) {
    var obj = this
    console.log(_session ,audio, "enter in load mediaaaa")
    if(audio["changingThisBreaksApplicationSecurity"]){
      audio = audio["changingThisBreaksApplicationSecurity"];
      obj.playURl = audio;
    }else{
      audio = audio;
      obj.playURl = audio;
    }
    var mediaInfo:any = new (<any>window).chrome.cast.media.MediaInfo(audio);
    console.log(mediaInfo , "mediainnfooooo");  
    // {"contentId":"https://s3.radio.co/s2781af807/listen","streamType":"buffered","metadata":null,"duration":null,"customData":null}
    mediaInfo.metadata = mediaInfo.metadata ? mediaInfo.metadata : {};
    mediaInfo.duration = mediaInfo.duration ? mediaInfo.duration : '0';
    mediaInfo.customData = mediaInfo.customData ? mediaInfo.customData : {};
    _session.loadMedia(new (<any>window).chrome.cast.media.LoadRequest(mediaInfo), function (media) {  
      obj._media = media;
      obj.session = _session;

      if(fromPage === 'radio-player'){
        obj.castingActive = true;
        obj.castingPage = fromPage;
      }else if(fromPage ==='sermon-detail'){
        obj.castingActive = true;
        obj.castingPage = fromPage;
      }
      else if(fromPage ==='audio-archieve'){
        var numeroPerguntas = 10; 
        obj.isCastingActive = Array.apply(null, new Array(numeroPerguntas)).map(function(){return false});
        obj.castingPage = fromPage;
        obj.castingActive = false;
        obj.isCastingActive[index] = true;
      }

      if(obj.common.isAudioPlay == "Play"){
          obj.event_provider.returnPlayPause("Play", audio);
          obj.event_provider.playingFlags("Pause");
      }
      console.log(obj._media, "media played")
    }, function (err) {
      console.log("error from load media" , err);
      obj._media = '';
    })
  }


  leaveSession(){
    console.log("emterrrrrrr")
      var obj = this;
    (<any>window).chrome.cast.media.StopRequest( function (success) {
      console.log("console.log successssss", success)
      obj._media = '';
      obj.session = '';
    },function (error){
      console.log("console.log successssss", error)
    });
    // _session.loadMedia(new (<any>window).chrome.cast.media.LoadRequest(mediaInfo), function (media) {  
  }

 
 

}



