  start(url: any) {
    // console.log(this.audioUrl , "-------------------------")
    if (url || this.audioUrl) {
      this.display_media_player_loader = true;
      console.log(url, "urllllllll");
      if (url) {
        let self = this;
        if (this.player) {
          this.player.stop();
        }
        this.common.isAudioPlay = "Play";
        this.player = new Howl({
          src:
            url["changingThisBreaksApplicationSecurity"] ||
            this.audioUrl["changingThisBreaksApplicationSecurity"],
          format: ["mp3", "aac"],
          html5: true,
          onplay: () => {
            this.isPlaying = true;
            this.activeTrack = true;
            this.updateProgress();
          },
          onend: () => {
            console.log("song is ended..........");
            this.togglePlayer(true);
            this.common.oldSong = [];
            this.common.isAudioPlay = "";
          },
        });

        this.player.once("load", () => {
          this.display_media_player_loader = false;
          self.current_sound_id = self.player.play();
          self.current_total_duration = self.intoMinutes(
            self.player.duration(self.current_sound_id)
          );
          console.log(
            this.current_total_duration,
            "------" + self.current_sound_id + "-------",
            "lllllllllll"
          );
          if (self.current_total_duration.includes(".00")) {
            self.current_total_duration = self.current_total_duration.replace(
              ".00",
              ""
            );
            console.log(this.current_total_duration, "lllllllllll");
          }

          this.firstPlay = false;
        });
      }
    } else {
      alert("Please select any audio before to play it. ThankYou!!");
    }
  }

  intoMinutes(second) {
    var minutes: any = Math.floor(second / 60);
    var seconds: any = Math.floor(second - minutes * 60);
    if (minutes < 10) {
      minutes = "0" + minutes;
    }
    if (seconds < 10) {
      seconds = "0" + seconds;
    }

    return minutes + ":" + seconds;
  }

  togglePlayer(pause) {
    this.isPlaying = !pause;
    this.firstPlay = false;
    if (pause) {
      this.player.pause();
      this.common.isAudioPlay = "Pause";
      this.event_provider.returnPlayPause("Play", this.audioUrl);
    } else {
      this.player.play();
      this.event_provider.returnPlayPause("Pause", this.audioUrl);
      this.common.isAudioPlay = "Play";
    }
  }

  seek() {
    let newValue = +this.range.value;
  }

  updateProgress() {
    let seek = Math.floor(this.player.seek());
    this.current_duration = this.intoMinutes(seek);
    this.progress = (seek / this.player.duration()) * 100 || 0;
    setTimeout(() => {
      this.updateProgress();
    }, 100);
  }

  forward() {
    let seek = Math.floor(this.player.seek());
    this.player.seek(seek + 10);
  }

  backward() {
    let seek = Math.floor(this.player.seek());
    this.player.seek(seek - 10);
  }

