  fancingInterval(userID?){
    console.log("geo messages")
    this.apiService.geofancingData({church_id:environment.church_id,donor_id:userID}).subscribe(async (res:any)=>{
      var getdata:any = res;
     
      console.log("geo messages", res)
      if(res.status){
        this.fancing_positions = await getdata.URL
        this.fancing_positions.forEach((geo , i) => {
          this.geoFencesArray.push({
            id: geo.id,
            lat: geo.latitude,
            lon: geo.longitude,
            identifier: geo.id,
            radius: geo.radius,
            latitude: geo.latitude,
            longitude: geo.longitude,
            notifyOnEntry: JSON.parse(geo.enter).title != '' ? true : false,
            notifyOnExit: JSON.parse(geo.exit).title != '' ? true : false,
            extras: {
              enter : JSON.parse(geo.enter),
              exit : JSON.parse(geo.exit),
            }
          })
        })
        setTimeout(() => {
          // this.configureBackgroundGeolocation();
        }, 5000);
        // this.geolocation.getCurrentPosition().then(pos=>{
        //   console.log(pos , "postions")
        //   this.radiusFinder(pos.coords)
        // })
      }else{
        console.log(res.Message)
      }
    })
  }

