import { Component, OnInit } from '@angular/core';
import { ModalController,NavParams } from "@ionic/angular";
import { ApiService } from 'src/services/api.service';
import {ToastService} from 'src/services/toast.service';
// import { Clipboard } from '@ionic-native/clipboard/ngx';






@Component({
  selector: 'app-emoji-for-action',
  templateUrl: './emoji-for-action.component.html',
  styleUrls: ['./emoji-for-action.component.scss'],
})
export class EmojiForActionComponent implements OnInit {
  toggled: boolean = true;
  message: string = '';
  type: any;
  data: any;
  alertMessage: any;

        
      constructor(public modalController: ModalController, private navParams: NavParams, private apiService: ApiService,
        private toast: ToastService) { }

  ngOnInit() {
    this.data = this.navParams.data;
 
    this.toggled = true
    
  }
  ionViewWillEnter(){
 
    this.toggled = true
  }

  reportMsg(){
        
        const body = JSON.stringify({
            message_id: this.data.message_id,
            account_donor_id: this.data.account_donor_id,
            chatroom_id: this.data.chatroom_id,
            message: 'Message Reported'
        });
        this.apiService.repMsg(body).subscribe((res:any)=>{
            if(res){
              this.toast.presentToastWithOptions(res.message,'Thanks','success');
              this.dismiss();
            }  
        })
    }

    copyMsg(){
   navigator.clipboard.writeText(this.data.message.message_text);

   this.toast.presentToastWithOptions(' Copied ', '','success');
      this.dismiss();
    }

  handleSelection(event) {
    this.message += event.char;

    const body = JSON.stringify({
      message_id: this.data.message_id,
      account_donor_id: this.data.account_donor_id,
      chatroom_id: this.data.chatroom_id,
      react: ''+event.char
  });
    this.apiService.reactMsg(body).subscribe((res:any)=>{
    
      if(res){
      }
      this.dismiss(body);
    })
   
  }
  dismiss(emoji?) {
    this.modalController.dismiss({
      'dismissed': true,
      "data":emoji
    });
  }
}
