import { Component, OnInit } from '@angular/core';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { ModalController, NavParams, Platform } from '@ionic/angular';
import { environment } from 'src/environments/environment';
import { AudioplayerService } from 'src/services/audioplayer.service';
import { DownloadService } from 'src/services/download.service';
import { ToastService } from 'src/services/toast.service';

@Component({
  selector: 'app-bible-sharing',
  templateUrl: './bible-sharing.component.html',
  styleUrls: ['./bible-sharing.component.scss'],
})
export class BibleSharingComponent implements OnInit {
  todayStudy:any;
  studyType:any;
  xDown = null;
  yDown = null;
  totalAudios:any=0;
  passageHide:boolean =false;
  constructor(public modalController: ModalController, public navParams:NavParams, public toast:ToastService, 
    public socialSharing:SocialSharing, public platform:Platform, public downloadService:DownloadService, public audioService:AudioplayerService) { }

  ngOnInit() {
    this.todayStudy = (this.navParams.get('todayStudy')) ? JSON.parse(this.navParams.get('todayStudy')) : "";
    this.studyType = this.navParams.get('studyType');
    this.totalAudios = this.navParams.get('length');
    this.passageHide = this.navParams.get('passageHide') ? this.navParams.get('passageHide') : false;
    console.log(this.todayStudy, this.passageHide);
  }

  dismiss() {
    this.modalController.dismiss({
        'dismissed': true
    });
}

passenger(){
  this.modalController.dismiss({
    'dismissed': true,
    data:'passenger'
});
}

shareURL(){
  if(this.platform.is("cordova")){
    this.socialSharing.share(`Study Thru The Bible - ${this.todayStudy.name} \n`+ environment.cmp_audioLink+this.todayStudy.uuid ).then(data =>{
    }, error => {
    });
    this.dismiss();
  }else{
  navigator.clipboard.writeText(environment.cmp_audioLink + this.todayStudy.uuid);
  this.toast.presentToastWithOptions(' Copied ', '', 'success');
  this.dismiss()
  }
}


markRead(){
  this.todayStudy['book_id'] = this.todayStudy.book_id? this.todayStudy.book_id: this.todayStudy.cmp_book_id; 
 this.audioService.heardStudy("05:00",this.todayStudy,this.studyType,true , this.totalAudios);
 this.toast.presentToastWithOptions('Study Completed', '', 'success');
 this.dismiss();
}


downloadStudy(){
this.downloadService.downloadsLocalSave(this.todayStudy,this.studyType);
this.dismiss();
}
close(){
  this.dismiss();
}

getTouches(evt) {
  return evt.touches ||             // browser API
      evt.originalEvent.touches; // jQuery
}
handleTouchStart(evt) {
  const firstTouch = this.getTouches(evt)[0];
  this.xDown = firstTouch.clientX;
  this.yDown = firstTouch.clientY;
};

handleTouchMove(evt) {
  if ( ! this.xDown || ! this.yDown ) {
    return;
  }

  var xUp = evt.touches[0].clientX;
  var yUp = evt.touches[0].clientY;

  var xDiff = this.xDown - xUp;
  var yDiff = this.yDown - yUp;

  if ( Math.abs( xDiff ) > Math.abs( yDiff ) ) {/*most significant*/
    if ( xDiff > 0 ) {
      /* left swipe */
    } else {
      /* right swipe */
    }
  } else {
    if ( yDiff > 0 ) {
      /* up swipe */
      console.log("console to swip up ");
    } else {
      /* down swipe */
      console.log("console to swip down ");
      // this.service.defaultValue = "revertChange"
      this.dismiss();

    }
  }
  /* reset values */
  this.xDown = null;
  this.yDown = null;
};



}
