import { Injectable } from '@angular/core';
import { Media, MediaObject } from '@ionic-native/media/ngx';
import { Platform } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class NativeAudioServiceService {

  constructor(private platform: Platform, private media: Media) { }

   onStartMedia(url): any{
     let return_value = false;
    const file: MediaObject = this.media.create(url);
    // to listen to plugin events:
    // file.seekTo(1);
    file.onStatusUpdate.subscribe(status =>{
       console.log('status', status);
      //  file.seekTo(1);
      
        console.log('getDuration()',file.getDuration());     
       console.log('getCurrentPosition', file.getCurrentPosition());
       if(file.getDuration() === -1){
        return false;
       } else {
         return true;
       }
      }); // fires when file status changes

    file.onSuccess.subscribe(() => console.log('Action is successful'));

    file.onError.subscribe(error => console.log('Error!', error));

    // play the file
    
    file.setVolume(1);
    // return true
  }


}
